.card {
  display: flex;
  flex-direction: column;
  padding: 32px 16px 16px;
  width: $card-width;
  border: 1px solid $border-color;
  border-radius: 5px;
  transition: transform .3s;

  &__image {
    margin: 0 auto 40px;
    width: 160px;
    height: 134px;
  }

  &__product {

    &-name {
      margin-bottom: 4px;
      font-weight: 500;
      font-size: 12px;
      line-height: 18px;
      color: $main-color;
    }
    &-code {
      margin-bottom: 16px;
      font-size: 10px;
      line-height: 14px;
      color: $secondary-color;
    }
  }

  &__stars-review {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 24px;
  }

  &__reviews {
    font-size: 10px;
    line-height: 14px;
    color: $main-color;
  }

  &__price {

    &-all {
      display: flex;
      justify-content: space-between;
      margin-bottom: 16px;
    }

    &-word {
      font-size: 12px;
      line-height: 18px;
      color: $secondary-color;
    }

    &-value {
      font-weight: 700;
      font-size: 16px;
      line-height: 18px;
      color: $main-color;
    }
  }

  &__button-link {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 166px;
    height: 40px;
    border: 1px solid transparent;
    border-radius: 5px;
    background-color: $blue-color;
    font-weight: 700;
    font-size: 14px;
    line-height: 16px;
    text-decoration: none;
    text-transform: uppercase;
    color: #fff;

    &:hover {
      color: $blue-color;
      background-color: #fff;
      border: 1px solid $blue-color;
    }
  }

  &:hover {
    transform: scale(1.2);

    .card__product-name {
      color: #34568b;
    }
  }
}
